	module.exports = " <div class=view-box _v-4932da75=\"\"> <div class=\"acc-mains scroll-box scroll-box-top scroll-box-bottom\" _v-4932da75=\"\"> <div class=protocol-wrap _v-4932da75=\"\"> <h2 _v-4932da75=\"\">个人信息采集授权书</h2> <span _v-4932da75=\"\">杭州畅时贷网络科技有限公司：</span> <p _v-4932da75=\"\">本人 {{params.name}} （ 身份证号码：{{params.idcard}} ）</p> <span class=no-b _v-4932da75=\"\">同意贵司在本人向贵司运营的 畅享时贷（含长富贷） 平台申请办理相关业务和其后的相关风险管理中，授权贵司自行或通过第三方数据服务机构依法向中国人民银行个人信用信息基础数据库及其他经法律法规或中国人民银行认可的信息系统、信息数据库及第三方征信机构查询、打印、使用、保存本人的各项基本信息（包括但不限于身份信息、财产信息、工作信息、通信信息、消费信息、交通信息、贷款信息）和信用报告用于：</span> <span class=no-b _v-4932da75=\"\">1、审核个人信贷业务借款申请；</span> <span class=no-b _v-4932da75=\"\">2、对已发放的个人借款进行贷后管理及逾期诉讼及保全；</span> <span class=no-b _v-4932da75=\"\">3、受理法人或其他组织的贷款申请或作为担保人，需要查询其法定代表人及出资人信用状况；</span> <span class=no-b _v-4932da75=\"\">4、其他与借款相关的各项用途。</span> <p _v-4932da75=\"\">同时本人授权贵司依法向上述系统、数据库或机构提供本人的各项基本信息和信用情况。但超出授权用途的后果及法律责任由贵司承担。</p> <span class=txt-r _v-4932da75=\"\">授权人（签章）：{{params.name}}</span> <span class=txt-r _v-4932da75=\"\"> {{dateArr[0]}} 年 {{dateArr[1]}} 月 {{dateArr[2]}} 日</span> </div> </div> </div> ";

