	module.exports = " <div class=view-box _v-52a26bfd=\"\"> <div class=\"acc-mains scroll-box scroll-box-top scroll-box-bottom\" _v-52a26bfd=\"\"> <img src=" + __webpack_require__(8) + " width=100% _v-52a26bfd=\"\"> <div class=tab-switch _v-52a26bfd=\"\"> <div :class=\"['tab-switch-item', {'active': selectIndex == 0}]\" @click=tabSwitch(0) _v-52a26bfd=\"\"> <div class=img _v-52a26bfd=\"\"><img src=" + __webpack_require__(17) + " _v-52a26bfd=\"\"></div> <span class=txt _v-52a26bfd=\"\">征信认证</span> </div> <div :class=\"['tab-switch-item', {'active': selectIndex == 1}]\" @click=tabSwitch(1) _v-52a26bfd=\"\"> <div class=img _v-52a26bfd=\"\"><img src=" + __webpack_require__(18) + " _v-52a26bfd=\"\"></div> <span class=txt _v-52a26bfd=\"\">公积金认证</span> </div> <div :class=\"['tab-switch-item', {'active': selectIndex == 2}]\" @click=tabSwitch(2) _v-52a26bfd=\"\"> <div class=img _v-52a26bfd=\"\"><img src=" + __webpack_require__(19) + " _v-52a26bfd=\"\"></div> <span class=txt _v-52a26bfd=\"\">社保认证</span> </div> </div> <div class=tab-list _v-52a26bfd=\"\"> <div :class=\"['tab-list-item', {'active': selectIndex == 0}]\" _v-52a26bfd=\"\"> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">01</span> <span class=txt _v-52a26bfd=\"\">登录中国人民银行征信中心</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con1 src=" + __webpack_require__(9) + " _v-52a26bfd=\"\"> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">02</span> <span class=txt _v-52a26bfd=\"\">点击马上开始</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con2 src=" + __webpack_require__(10) + " _v-52a26bfd=\"\"> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">03</span> <span class=txt _v-52a26bfd=\"\">开始并完成注册</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con3 src=" + __webpack_require__(11) + " _v-52a26bfd=\"\"> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">04</span> <span class=txt _v-52a26bfd=\"\">生成报告，等待征信中心所发短信验证码</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con4 src=" + __webpack_require__(12) + " _v-52a26bfd=\"\"> </div> </div> <div :class=\"['tab-list-item', {'active': selectIndex == 1}]\" _v-52a26bfd=\"\"> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">01</span> <span class=txt _v-52a26bfd=\"\">公积金账号密码获取</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con5 src=" + __webpack_require__(13) + " _v-52a26bfd=\"\"> <img class=con6 src=" + __webpack_require__(14) + " _v-52a26bfd=\"\"> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">02</span> <span class=txt _v-52a26bfd=\"\">可向贵司人事部门询问</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">03</span> <span class=txt _v-52a26bfd=\"\">个人携带身份证、公积金卡到公积金管理中心查询</span> </div> </div> <div :class=\"['tab-list-item', {'active': selectIndex == 2}]\" _v-52a26bfd=\"\"> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">01</span> <span class=txt _v-52a26bfd=\"\">社保账号密码获取</span> </div> <div class=content _v-52a26bfd=\"\"> <span class=line _v-52a26bfd=\"\"></span> <img class=con5 src=" + __webpack_require__(15) + " _v-52a26bfd=\"\"> <img class=con6 src=" + __webpack_require__(16) + " _v-52a26bfd=\"\"> </div> <div class=title _v-52a26bfd=\"\"> <span class=circle _v-52a26bfd=\"\">02</span> <span class=txt _v-52a26bfd=\"\">可携带身份证到各区社会保险经办机构业务办理大厅查询</span> </div> </div> </div> </div> </div> ";

